export default {
  layout: 'home',
  data() {
    return {
      left: '0',
      top: '0',
      popit: true,
    }
  },
  methods: {
    ChangePosition() {
      this.left = `${Math.floor(Math.random() * (40 + 50 + 1)) - 50}%`
      this.top = `${Math.floor(Math.random() * (300 - 0 + 1)) + 0}px`
    },
    PopIt() {
      this.popit = false
    },
  },
}
